.page-header{
    z-index:1000;
    position:sticky;
    height:0px;
    top :0;
    left:0;
    right:0;
    .navigation-bar{
        padding: 32px 0px;
        .logo{
            margin: 0px 30px 0px 0px;
            padding: 0px 15px;
            float:left;
        }
        .navigation-bar-item{
            .navbar {
                overflow: hidden;
                background-color:none;
              }
              
            .navbar a {
                float: left;
                font-size: 16px;
                text-align: center;
                padding: 14px 16px;
                text-decoration: none;
                font-weight:bold;
                color: #253237;
              }
              
              .dropdown {
                float: left;
                overflow: hidden;
              }
              
              .dropdown .dropbtn {
                font-size: 16px;  
                border: none;
                outline: none;
                padding: 14px 16px;
                background-color: inherit;
                font-family: inherit;
                margin: 0;
                font-weight:bold;
                color: #253237;
              }
              
              .navbar a:hover, .dropdown:hover .dropbtn {
                background-color: none;
              }
              
              .dropdown-content {
                display: none;
                position: absolute;
                background-color: #f9f9f9;
                min-width: 160px;
                box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
                z-index: 1;
              }
              
              .dropdown-content a {
                float: none;
                color: black;
                padding: 12px 16px;
                text-decoration: none;
                display: block;
                text-align: left;
              }
              
              .dropdown-content a:hover {
                background-color: #ddd;
              }
              
              .dropdown:hover .dropdown-content {
                display: block;
              }
            ul {
                list-style-type: none;
                margin: 0;
                padding: 0;
                overflow: hidden;
                padding : 0px 15px;
                li {
                    float: left;
                    a {
                        display: flex;
                        color:map-get($color,text-black);
                        text-align: center;
                        padding: 14px 16px;
                        text-decoration: none;
                        &:hover:not(.active) {
                            background-color:map-get($color,home-hover);
                        }
                    }
                }
            }
        }
    }
}
